<template>
    <section>
        <div class="inputBox shadow">
            <input type="text" v-model="newTodoItem" placeholder="Type what you have to do" v-on:keyup.enter="addTodo">
            <span class="addContainer" v-on:click="addTodo">
                <i class="addBtn fas fa-plus" aria-hidden="true"></i>
            </span>

            <modal v-if="showModal" @close="showModal = false">
                <h3 slot="header">경고</h3>
                <span slot="footer" @click="showModal = false">
                    할 일을 입력하세요.
                    <i class="closeModalBtn fas fa-times" aria-hidden="true"></i>
                </span>
            </modal>
        </div>
    </section>
</template>

<script>
import Modal from './common/Modal.vue'

export default {
    data(){
        return {
            newTodoItem: '',
            showModal: false
        }
    },
    methods: {
        addTodo(){
            if(this.newTodoItem !== ""){
                const value = this.newTodoItem && this.newTodoItem.trim();
                this.$emit('addTodo', value);
                this.clearInput();
            } else {
                this.showModal = !this.showModal;
            }
        },
        clearInput(){
            this.newTodoItem = '';
        }
    },
    components: {
        Modal: Modal
    }
}
</script>

<style>
    input:focus { outline: none; }
    .inputBox { background: #fff; height: 50px; line-height: 50px; border-radius: 5px; }
    .inputBox input { border-style: none; font-size: 0.9rem; } 
    .addContainer { float: right; background: linear-gradient(to right, #6478fb, #8763fb); display: block; width: 3rem; border-radius: 0 5px 5px 0; cursor: pointer; }
    .addBtn { color: #fff; vertical-align: middle; }
</style>